import { useState, useEffect } from 'react'

const usePassword = () => {
  const [userEmail,setUserEmail] = useState('')
  const [password, setPassword] = useState('')
  const [province,setProvince ] = useState('')

  useEffect(() => {
    // Retrieve the password from local storage on component mount
    const savedUserEmail = localStorage.getItem('email')
    const savedPassword = localStorage.getItem('password')
    const savedProvince = localStorage.getItem('province')
    setUserEmail(savedUserEmail)
    setPassword(savedPassword)
    setProvince(savedProvince)
  }, [])

  const setPasswordAndStore = (newEmail,newPassword,newProvince) => {
    console.log(newProvince)
    if(newEmail.length!=0){
      localStorage.setItem('email', newEmail )
      setUserEmail(newEmail)
    }
    if(newPassword.length!=0){
      localStorage.setItem('password', newPassword)
      setPassword(newPassword)
    }
    if(newProvince.length!=0){
      localStorage.setItem( 'province',newProvince)
      setProvince(newProvince)
    }
  }

  const removeUserDetail = () => {
    localStorage.removeItem('email')
    localStorage.removeItem('password')
    localStorage.removeItem('province')
  }

  return { userEmail ,userPassword: password, setUserDetail: setPasswordAndStore, province, setProvince , removeUserDetail}
}

export default usePassword